<template>
  <div>
      <!-- 这是项目管理 -->
      <div class="title">
        项目管理
        <!-- <button @click="Fnprojectlist">项目列表</button> -->
      </div>
      <div class="MFprojectManagementBox">
        <!-- 对项目的增删改查start -->
        <div class="jieGou_tit">
          项目列表
          <el-divider></el-divider>
        </div>
         <!-- 项目展示start -->
          <div class="projecList" v-for="(item,index) in DprojecList" :key="index" @click="Fngotolsit(item.project_id)">
            <div class="projectName">{{item.p_name}}</div>
            <div class="projectTime">{{item.p_province}}</div>
          </div>
         <!-- 项目展示end -->

      </div>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from "vuex";
export default {
  data(){
    return{
      DprojecList:[],//项目列表

    };
  },
  methods:{
    handleNodeClick(data) {//树形结构触发事件
      console.log(data);
    },
    Fngotolsit(project_id){//去单个项目的详情
      this.$router.push({
        path:'/index/MFseeProjectManagement', //也可以在路径上拼接
        query:{
          project_id:project_id,       //传递的值
          company_id:this.userInfo.u_company_id,
        }
      })
    },
    Fnprojectlist(){//这是项目列表
      url.projectlist({
        user_id:this.userInfo.user_id,
        company_id:this.userInfo.u_company_id
      }).then(res=>{
        console.log(res,'--------------这是项目列表')
          let xx=res.data.data
          let newlist=[]
          xx.forEach(element => {
          element.fields.project_id=element.pk
          newlist.push(element.fields)
          });
          this.DprojecList=newlist
      })
    }
  },
  computed:{
    ...mapState(['userInfo'])
  },
  mounted(){
    this.Fnprojectlist()//这是项目列表
  }
}
      
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.MFprojectManagementBox
  padding 30px
  min-width 950px
  .jieGou_tit
    margin-bottom 17px
    .jieGou_tit_rigth
      width 110px
      float right
  .custom-tree-node
    width 100%
    display inline-block

  .terrList_right
    display inline-block
    float right
    font-size 14px
    color #999
.projecList
  border 1px solid #cccccc
  border-radius 6px
  padding 8px
  margin-bottom 9px
  font-size 16px
  background #F2F6FC
  color #666
  .projectTime
    float right
    margin-top -14px
    font-size 14px
    color #99
.projecList:hover
  background #C0C4CC
</style>


